<template>
  <tas-base-crud :config="config" />
</template>

<script>
export default {
  name: 'crud-m-project-categories',
  data () {
    return {
      config: {
        title: 'Kategori Proyek',
        model_api: 'm-project-categories',
        getter: 'm-project-categories',
        setter: 'm-project-categories',
        pk_field: 'name',
        permission: {
          create: 'create-m-project-categories',
          read: 'view-m-project-categories',
          update: 'update-m-project-categories',
          delete: 'delete-m-project-categories'
        },
        fields: [
          {
            id: 'active',
            data: 'active',
            label: 'Status',
            placeholder: null,
            methods: {
              list: {
                order: true,
                class: { 0: 'badge badge-danger', 1: 'badge badge-primary' },
                transform: 'active'
              },
              detail: {
                order: true,
                class: { 0: 'badge badge-danger', 1: 'badge badge-primary' },
                transform: 'active'
              },
              create: {
                view_data: 'active',
                type: 'radio',
                value: '1',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              },
              update: {
                view_data: 'active',
                type: 'radio',
                value: '1',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              },
              filter: {
                view_data: 'active',
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: '1' }, { label: 'Non Aktif', code: '0' }]
                  }
                }
              }
            }
          },
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_at', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'created_by', label: 'Dibuat Oleh', methods: { list: false, create: false, update: false, filter: false } },
          { id: 'name', label: 'Nama Kategori' }
        ]
      }
    }
  }
}
</script>



